
.container-empresa{
    /* margin: 2rem 0px 5rem 0px; */
    margin: 2rem auto;
}

.background-img{
    height: 100%;
    width: 100%;
    border-radius: 10px;

}
.description-card{
    text-align: left;
}

.description-card #sub-title{
    font-weight: 400;
    letter-spacing: 7px;
    font-size: 1.6rem;
    color: #FCD200;
    font-family:  Sans-serif;
}


.description-card  #title{
    font-size: 2.5rem;
    letter-spacing: -0.3px;
    font-weight: 900;
    margin-bottom: 35px;
}
.btn-line{
    padding: 7px 42px;
    border-radius: 5px;
    font-weight: 500;
    border: 1.9px solid #212529;
    background: transparent;
    
}

.btn-line:hover{
    background: #212529;
    color: white;
    font-weight: 600;
}
.btn-ver-empresa{
    margin-top: 10px;
    background-color: #ffffff00;
    padding: 6px 42px;
    /* width: 22%; */
    text-align: center;
    border-radius: 5px;
    font-weight: 600;
    color: rgb(53 53 53);
    border: 1.3px solid #212529;
  }
  .btn-ver-empresa:hover{
    background-color: #212529;
    font-weight: 600;
    color: #fafafa;
    border: 1.3px solid #212529;
  }