
.noGuias {
	font-size: 15px;
}
.title-buscar {
	text-align: start;
}
.error{
	color: rgb(196, 4, 4);
	font-size: 14px;
	font-weight: 400;
	text-align: start;
}

.counter{
	font-size: 16px !important;	
}

.privincia-onList {
	overflow: hidden;
	text-overflow: ellipsis;
	width: 150px;
	background: rebeccapurple;
	display: -webkit-box;
	-webkit-line-clamp: 1; /* number of lines to show */
	-webkit-box-orient: vertical;
}

.item-prv{
	max-width: auto ; 
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	display: -webkit-box;
	-webkit-line-clamp: 1; /* number of lines to show */
	-webkit-box-orient: vertical;
}




.delete {
	cursor: pointer !important;
	
	color: rgb(231, 116, 116);
}
