

.bannertop{
    margin-top: 4em;
    height: 22rem;
    margin-bottom: 4rem;
    width: 100%;
    position: sticky;
    background-image: url("../../img/valija.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}
.bannertop .title-nosotros{
    font-size: 75px;
    padding-top: 9rem;
    color: #ffffffea;
    line-height: 0.8em;
    font-weight: 800;
    text-transform: capitalize;
}
.bannertop::before{
    position: absolute;
    top: 0;
    left: 0;
    height: 22rem;
    width: 100%;
    content: "";
    background: rgba(0, 0, 12, 0.548);
    z-index: -1;
}
.nosotros .row .service-box {
    box-shadow: 0px 0 30px rgba(1, 41, 112, 0.08);
    height: 100%;
    padding: 60px 30px;
    text-align: center;
    transition: 0.3s;
    border-radius: 5px;
}

.img-bienvenida{
    height: 100%;
    background-color: red;
}
.back-nosotros{
    padding: 10rem 0 1rem 0;
}
.data{
    padding: 3rem 0 3rem 0;
}
.nosotros-text{
    color: #222631;
    font-size: 220px;
    line-height: 0.8em;
    font-weight: 800;
    font-family: var(--thm-font);
    letter-spacing: -0.036em;
    opacity: 0.07;
}
.nostros-text-container{
    position: absolute;
    top: 67rem;
    left: -330px;
    transform: rotate( 
-90deg
 );


}
 .right{
    position: relative;
    display: inline-block;
    width: 124px;
    height: 4px;
    background: var(--thm-base);
    margin-left: 14px;
    top: -11px;
    background-color: #f94d1c;
}

.right-top{
    position: relative;
    display: inline-block;
    width: 124px;
    height: 4px;
    background: var(--thm-base);
    margin: 0px 20px   0px 20px;
    top: -11px;
    background-color: #f5f5f5;
}


@media screen and (max-width:520px) {
    .bannertop .title-nosotros{
       padding-top: 4.5rem;
    }
}