/*--------------------------------------------------------------
# Services
--------------------------------------------------------------*/

.services {
  padding: 0rem 0rem 7rem 0rem;
}
.services .service-box {
  box-shadow: 0px 0 30px rgba(1, 41, 112, 0.08);
  height: 100%;
  padding: 60px 30px;
  text-align: center;
  transition: 0.3s;
  border-radius: 5px;
}

.section-heading {
  color: #30384e;
  font-size: 2.5rem;
  letter-spacing: -0.3px;
  font-weight: 900;
}

.section-subheading {
  font-weight: 400;
  letter-spacing: 5px;
  font-size: 20px;
  color: #30384e85;
  font-family: Sans-serif;
  margin-bottom: 30px;
}

.services .service-box .icon {
  font-size: 36px;
  padding: 40px 20px;
  border-radius: 4px;
  position: relative;
  margin-bottom: 25px;
  display: inline-block;
  line-height: 0;
  transition: 0.3s;
}

.services .service-box h3 {
  color: #444444;
  font-weight: 700;
}

.services .service-box .read-more {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 16px;
  padding: 8px 20px;
}

.services .service-box .read-more i {
  line-height: 0;
  margin-left: 5px;
  font-size: 18px;
}

.services .service-box.blue {
  border-bottom: 3px solid #2db6fa;
}

.services .service-box.blue .icon {
  color: #2db6fa;
  background: #dbf3fe;
}

.services .service-box.blue .read-more {
  color: #2db6fa;
}

.services .service-box.blue:hover {
  background: #2db6fa;
}

.services .service-box.orange {
  border-bottom: 3px solid #f68c09;
}

.services .service-box.orange .icon {
  color: #f68c09;
  background: #fde3c4;
}

.services .service-box.orange .read-more {
  color: #f68c09;
}

.services .service-box.orange:hover {
  background: #f68c09;
}

.services .service-box.green {
  border-bottom: 3px solid #08da4e;
}

.services .service-box.green .icon {
  color: #08da4e;
  background: #cffddf;
}

.services .service-box.green .read-more {
  color: #08da4e;
}

.services .service-box.green:hover {
  background: #08da4e;
}

.services .service-box:hover h3, .services .service-box:hover p, .services .service-box:hover .read-more, .services .service-box:hover .logo_services1, .services .service-box:hover .logo_services2, .services .service-box:hover .logo_services3,
.services .service-box:hover h6 {
  color: white;
  fill: #fff;
}

.services .service-box h3 {
  margin-top: 20px;
}

.services .service-box:hover .icon {
  background: #fff;
}

.logo {
  width: 100%;
  height: 6rem;
}

.logo_services1 {
  height: 6rem;
  fill: #2db6fa;
}

.logo_services2 {
  height: 6rem;
  fill: #f68c09;
}

.logo_services3 {
  height: 6rem;
  fill: #08da4e;
}
.btn-ver{
  margin-top: 10px;
  background-color: #ffffff00;
  padding: 6px 42px;
  /* width: 22%; */
  text-align: center;
  border-radius: 5px;
  font-weight: 600;
  color: rgb(53 53 53);
  border: 1.3px solid rgb(13, 18, 85);
}
.btn-ver:hover{
  background-color: #ffffff4f;
  font-weight: 600;
  color: #fafafa;
  border: 1.3px solid #ffffff;
}

.img_play {
  padding-left: 10px;
  width: 150px;
}


.services {
  padding: 0rem 0rem 7rem 0rem;
}