
.coverturas-container {
    margin: .5rem;
    background-color: #30384e;
    background-image: url("../../../img/coverturas.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 68vh;
    width: 97%;
    border-radius: 15px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    box-shadow: 1px 1px 25px 2px rgb(41 41 41 / 16%);
}
.btn-vermas{
    margin: 20px 21px;
    padding-inline: 60px;
}