.noGuias {
	font-size: 15px;
}

.container-spinner-buscar {
	display: flex;
	align-items: center;
}

#table-to-excel {
	display: none;
}

.btn-excel {
	background-color: #3d885f;
	border: none;
	box-shadow: 0 2px 15px rgba(0, 0, 0, 0.288);
	padding: 10px 12px;
	border-radius: 8px;
	color: #fff;
	font-size: 12px;
}

.container-errorCrear {
	background-color: rgb(255, 28, 28);
	padding: 8px 250px 8px 8px;
	right: 1%;
	top: 1%;
	min-width: 30%;
	z-index: 1;
	border-radius: 4px;
	position: absolute;
}

.container-errorCrear p {
	color: #fff;
	margin: 0;
	text-align: left;
}

.overflow-text {
	width: 100px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.actions {
	padding: 5px;
	margin-left: 5px;
	padding-bottom: 0px;
	border-radius: 7px;
}

.print {
	background-color: rgb(121, 130, 173);
	color: white;
}
.view {
	background-color: cornflowerblue;
	color: white;
}

.file {
	background-color: rgb(126, 173, 121);
	color: white;
}

.download {
	background-color: rgb(121, 173, 134);
	color: white;
}
.anulacion {
	background-color: rgb(252, 106, 106);
	color: rgb(255, 255, 255);
}

.size-description{
	width: 250px !important;
	overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
}

.block-dowload{
	width: 210mm;
	min-height: 297mm;
	margin: 10mm auto;
	background: white;
	box-shadow: 0 0 0.5cm rgba(0,0,0,0.5);
}
.seter-position{
	position: absolute;
    z-index: 2;

}
.rol-text{
	font-size: 14px;
}
