.body {
    display: -webkit-box;
    display: flex;
    -ms-flex-align: center;
    -ms-flex-pack: center;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    padding-top: 40px;
    padding-bottom: 40px;
    background-color: #f5f5f5;
    height: 100vh;
}

.img {
    background-image: url("../../img/rapifast_logo_blue.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    height: 75px;
    width: 70%;
    margin: auto;
}

.form-signin {
    width: 35%;
    padding: 15px;
    margin: 0 auto;
    border-radius: 15px;
}

.form-signin input[type="password"] {
    margin-bottom: 10px;
    padding: 15px 10px;
    border-radius: 5px;
    /* border-top-right-radius: 0; */
}

.form-signin input[type="email"] {
    padding: 15px 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    /* border-bottom-right-radius: 0;
    border-bottom-left-radius: 0; */
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    -webkit-clip-path: inset(50%);
    clip-path: inset(50%);
    border: 0;
}

.font-weight-normal {
    font-weight: 400!important;
}

.btn-back {
    color: grey;
    text-decoration: none;
}

.btn-back:hover {
    color: rgba(0, 0, 0, 0.767);
    font-weight: 600;
}

.text-error-red {
    color: red;
    padding-top: .5rem;
    padding-left: .5rem;
    margin: 0;
    text-align: left;
    font-size: 15px;
    font-weight: 400;
}
.bt-h{
    height: 20px;
}

.icon-eye{
    padding-top: 4px !important;
    margin-bottom: -4px !important;
}
@media screen and (max-width:1400px) {
    .form-signin {
        width: 40%;
    }
}

@media screen and (max-width:1200px) {
    .form-signin {
        width: 50%;
    }
}

@media screen and (max-width:990px) {
    .form-signin {
        width: 70%;
    }
}

@media screen and (max-width:575px) {
    .form-signin {
        width: 80%;
    }
}

@media screen and (max-width:450px) {
    .form-signin {
        width: 90%;
    }
}