
.table-cm{
    padding: 0 1%;
}

.btn-dt-Anular{
    color: #ffbc00 ;
    background-color: #727df500 ;
    border-color: #fa5c7c00 ;
    font-size: 14px;
    font-weight: 500 ;
    border: 1px solid #ffbc00 !important;
    border-radius: 4px;
    padding: 2px 0;
}

.btn-dt-Anular:hover{
    color: rgb(255, 255, 255) ;
    font-weight: 500 ;
    border: none;
    border: 1px solid #ffbc00 !important;
    background-color: #ffbc00;
    transition: 300ms;
    -webkit-box-shadow: 0 2px 6px 0 #48494b28 !important;
    box-shadow: 0 2px 6px 0 #48494b28  !important;

}
.bt-a{
    font-weight: 500;
    font-size: 15px;
}

.truencate-id{
    width: 100px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}