@media print {
    @page {
        size: A4;
        margin: 180mm;
    }
    html, body {
        width: 1024px;
    }
    body {
        margin: 0px auto;
        line-height: 1em;
        word-spacing: 1px;
        letter-spacing: 0.2px;
        background: white;
        color: black;
        width: 100%;
        float: none;
    }
    .manifiesto-page {
        /* margin-top: 50px;
        margin-left: 25px;
        margin-right: 20px; */
        margin: 40px 30px 0px 30px;
    }
    .title-manifiesto {
        font-weight: 700;
        font-size: 18px;
    }
    .left {
        border-left: 0.5px solid rgb(90, 90, 90);
    }
    .right-m {
        border-right: 0.5px solid rgb(90, 90, 90);
    }
    .top {
        border-top: 0.5px solid rgb(90, 90, 90);
    }
    .bottom {
        border-bottom: 0.5px solid rgb(90, 90, 90);
    }
    .separator-padding {
        padding-left: 15px;
    }
    .remove-m {
        margin-left: 0px !important;
        margin-left: 0px !important;
        padding-bottom: 0px !important;
        margin-bottom: 0px !important;
    }
    .item-code {
        padding-top: 10px;
    }
    .logoManifiesto img {
        width: 210px;
        margin-top: 20px;
    }
    .border-all {
        border-left: 0.5px solid rgb(90, 90, 90);
        border-right: 0.5px solid rgb(90, 90, 90);
        border-bottom: 0.5px solid rgb(90, 90, 90);
    }
}

.manifiesto-page {
    /* margin-top: 50px;
    margin-left: 25px;
    margin-right: 20px; */
    margin: 30px 30px 0px 30px;
}

.title-manifiesto {
    font-weight: 700;
    font-size: 18px;
}

.left {
    border-left: 0.5px solid rgb(90, 90, 90);
}

.right-m {
    border-right: 0.5px solid rgb(90, 90, 90);
}

.top {
    border-top: 0.5px solid rgb(90, 90, 90);
}

.bottom {
    border-bottom: 0.5px solid rgb(90, 90, 90);
}

.separator-padding {
    padding-left: 15px;
}

.remove-m {
    margin-left: 0px !important;
    margin-left: 0px !important;
    padding-bottom: 0px !important;
    margin-bottom: 0px !important;
}

.item-code {
    padding-top: 10px;
}

.logoManifiesto img {
    width: 210px;
    margin-top: 20px;
}

.border-all {
    border-left: 0.5px solid rgb(90, 90, 90);
    border-right: 0.5px solid rgb(90, 90, 90);
    border-bottom: 0.5px solid rgb(90, 90, 90);
}

.separatorpage {
    height: 45px;
}

.separatorpage-last {
    height: 20px;
}

.marca-agua-m {
    position: absolute;
}

.img-marcaAgua-m {
    opacity: 0.17;
    width: 730px;
    margin-top: 90%;
    margin-left: 20%;
}

.text-description {
    font-size: 13px;
}

.limit-size-2 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    /* number of lines to show */
    -webkit-box-orient: vertical;
}

.limit-size-1 {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    /* number of lines to show */
    -webkit-box-orient: vertical;
}