.services .service-box {
    box-shadow: 0px 0 30px rgba(1, 41, 112, 0.08);
    height: 100%;
    padding: 60px 30px;
    text-align: center;
    transition: 0.3s;
    border-radius: 5px;
}

.sub-title {
    color: rgb(121, 121, 121);
    margin-bottom: 3rem;
}

.sub-title-2 {
    color: rgb(121, 121, 121);
    font-size: 16px;
    /* margin-bottom: 3rem; */
}

.tiempo {
    color: rgb(80, 80, 80);
    font-weight: 600;
}

.arrows {
    width: 60px;
    height: 80px;
    /* position: absolute; */
    /* left: 50%; */
    /* margin-left: -30px; */
    /* bottom: 10px; */
}

.arrows path {
    stroke: #030849;
    fill: transparent;
    stroke-width: 4px;
    animation: arrow 2s infinite;
    -webkit-animation: arrow 2s infinite;
}

.servicesss {
    padding: 7rem 0rem !important;
}

@keyframes arrow {
    0% {
        opacity: 0
    }
    40% {
        opacity: 1
    }
    80% {
        opacity: 0
    }
    100% {
        opacity: 0
    }
}

@-webkit-keyframes arrow
/*Safari and Chrome*/

    {
    0% {
        opacity: 0
    }
    40% {
        opacity: 1
    }
    80% {
        opacity: 0
    }
    100% {
        opacity: 0
    }
}

.arrows path.a1 {
    animation-delay: -1s;
    -webkit-animation-delay: -1s;
    /* Safari  Chrome */
}

.arrows path.a2 {
    animation-delay: -0.5s;
    -webkit-animation-delay: -0.5s;
    /* Safari  Chrome */
}

.arrows path.a3 {
    animation-delay: 0s;
    -webkit-animation-delay: 0s;
    /* Safari  Chrome */
}

@media screen and (max-width:500px) {
    .section-heading {
        font-size: 1.7rem!important;
        padding-bottom: 1rem;
    }
    .section-subheading {
        padding: 0 2.5rem!important;
        font-size: 1rem!important;
    }
}