.container-calculator {
    height: 100%;
    width: 100%;
    text-align: start;
    align-items: flex-start;
}
.border-input input{
    border-color: #212529;
}


.form-container {
    box-shadow: 1px 1px 25px 2px rgb(41 41 41 / 16%);
    padding: 50px 25px;
    margin: 14px 5px;
    background-color: white;
    border-radius: 10px;
}

.row {
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.row .form-group {
    margin-bottom: 1rem;
}

.laber-input {
    display: inline-block;
    margin-bottom: 0.5rem;
    font-weight: 600;
}

.input-content {
    border: none;
    -webkit-box-shadow: 0 1px 2px 0 rgb(0 0 0 / 10%);
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 10%);
    border-radius: 4px;
    height: 54px;
    background: #fff;
}



.div-infoSelected {
    border-radius: 7px;
    background-color: white;
    border: 1px solid #ccc!important;
    margin-left: -10px;
    padding-top: 12px;
}

.container-section {
    padding-top: 10px;
}

.end-section {
    padding-top: 1em;
}

.btn-cotizar {
    margin-top: 1em;
    width: 100%;
}

#text-btn {
    font-weight: 600;
    font-size: 18px;
    letter-spacing: 1px;
}

.pricing-contianer {
    padding: 7px 11px;
    margin-right: -25px;
    /* border-color: #212529; */
}

.price {
    border: 1px solid #212529!important;
    padding: 10px 10px 0px 12px;
    border-radius: 5px;
    margin: 0px 0px 0px 0px;
}

@media screen and (max-width:990px) {
    .div-infoSelected h5 {
        font-size: 1.1rem!important;
    }
    .div-infoSelected p {
        font-size: .8rem!important;
    }
}
@media screen and (max-width:760px) {
    .container-section{
        display: flex!important;
        flex-direction: column!important;
        justify-content: flex-start!important;
        align-items: flex-start!important;
    }
    .container-section div{
        width: 95%;
    }
    .container-section .margin-btn{
        margin-bottom: 10px;
    }
    .first{
        margin-top: .2rem!important;
        margin-bottom: .2rem!important;
    }
}