
.crear-guia-scroll{
	overflow-y: auto;
	height: 100vh;
}
.crear-guia-scroll::-webkit-scrollbar {
	width: 20px;
	           /* width of the entire scrollbar */
  }
  
  .crear-guia-scroll::-webkit-scrollbar-track {
	background: rgba(255, 255, 255, 0);        /* color of the tracking area */
  }
  
  .crear-guia-scroll::-webkit-scrollbar-thumb {
	background-color: rgba(77, 77, 77, 0.281);    /* color of the scroll thumb */
	border-radius: 20px;       /* roundness of the scroll thumb */
	/* border: 3px solid orange;  creates padding around scroll thumb */
  }
.crearGuias {
	background-color: #f6f7fa;
	position: relative;
	/* z-index: 1; */
}

.card {
	border: none;
	-webkit-box-shadow: 0 0 35px 0 #4747471e;
	box-shadow: 0 0 35px 0 #4747471e;
	margin-bottom: 24px;
	border-radius: 0.35rem !important;
}
.border-separator{
	border-right: 0.5px solid rgb(226, 226, 226);
}
.card-body {
	-webkit-box-flex: 1;
	-ms-flex: 1 1 auto;
	flex: 1 1 auto;
	padding: 1.5rem 1.5rem;
}

.titleSection-form {
	text-align: start;
	font-size: 15px;
	font-weight: 600;
	color: #6c757d;
}

.form-control {
	font-size: 0.9em !important;
	border-radius: 3px !important;
	padding: 11px 5px 11px 14px !important;
	color: #000000 !important;
}

.form-control:focus {
	border-color: rgba(68, 68, 68, 0.151) !important;
	-webkit-box-shadow: none !important;
	-moz-box-shadow: none !important;
	box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.075) inset, 0px 0px 8px rgba(95, 137, 230, 0.329) !important;
	background: rgba(127, 187, 236, 0.068) !important;
}
.complete-indo{
	background: #e8f0fe !important;
}
.form-select {
	font-size: 0.9em !important;
	border-radius: 3px !important;
	padding: 11px 5px 11px 14px !important;
	color: #6c757d !important;
}

.form-select:focus {
	border-color: rgba(100, 100, 100, 0.322) !important;
	-webkit-box-shadow: none !important;
	-moz-box-shadow: none !important;
	box-shadow: none !important;
}

.form-label-input {
	margin-bottom: .5rem;
	font-weight: 600;
	cursor: default;
	text-align: start;
	font-size: 15px;
	width: 100%;
}

.textA {
	text-align: start;
}

.text-description {
	font-size: 15px;
	font-weight: 400;
	color: #6c757d;
}

.btn-dt {
	color: #67696b;
	background-color: #c7bfc000;
	border-color: #fa5c7c00;
	font-weight: 500;
	border: 1px solid #ccc !important;
	border-radius: 4px;
}

.btn-dt:hover {
	color: #48494b;
	font-weight: 500;
	border: none;
	border: 1px solid #adadad !important;
	transition: 300ms;
	-webkit-box-shadow: 0 2px 6px 0 #48494b28 !important;
	box-shadow: 0 2px 6px 0 #48494b28 !important;
}

.btn-dt-delete {
	color: rgb(255, 255, 255);
	background-color: #fa5c7c;
	border-color: #fa5c7c00;
	font-size: 14px;
	font-weight: 500;
	border: 1px solid #ccc !important;
	border-radius: 4px;
	padding: 2px 0;
}

.btn-dt-delete:hover {
	color: rgb(255, 255, 255);
	font-weight: 500;
	border: none;
	border: 1px solid #d43d5c !important;
	background-color: #e0395b;
	transition: 300ms;
	-webkit-box-shadow: 0 2px 6px 0 #48494b28 !important;
	box-shadow: 0 2px 6px 0 #48494b28 !important;
}

.btn-dt-edit {
	color: rgb(255, 255, 255);
	background-color: #727cf5;
	border-color: #fa5c7c00;
	font-size: 14px;
	font-weight: 500;
	border: 1px solid #ccc !important;
	border-radius: 4px;
	padding: 2px 0;
}

.btn-dt-edit:hover {
	color: rgb(255, 255, 255);
	font-weight: 500;
	border: none;
	border: 1px solid #727cf5 !important;
	background-color: #5864eb;
	transition: 300ms;
	-webkit-box-shadow: 0 2px 6px 0 #48494b28 !important;
	box-shadow: 0 2px 6px 0 #48494b28 !important;
}

.btn-success {
	color: #fff !important;
	background-color: #0acf97 !important;
	border-color: #0acf97 !important;
	font-weight: 500 !important;
}

.btn-success:hover {
	-webkit-box-shadow: 0 2px 6px 0 rgb(10 207 151 / 50%) !important;
	box-shadow: 0 2px 6px 0 rgb(10 207 151 / 50%) !important;
	font-weight: 600 !important;
	transition: 300ms;
}

.btn-success:focus {
	border-color: rgba(100, 100, 100, 0) !important;
	-webkit-box-shadow: none !important;
	-moz-box-shadow: none !important;
	box-shadow: none !important;
}

.btn-success-disable {
	color: #fff !important;
	background-color: #727272 !important;
	border-color: #363636 !important;
	font-weight: 500 !important;
}

.btn-success:hover {
	-webkit-box-shadow: 0 2px 6px 0 rgb(10 207 151 / 50%) !important;
	box-shadow: 0 2px 6px 0 rgb(10 207 151 / 50%) !important;
	font-weight: 600 !important;
	transition: 300ms;
}

.btn-success:focus {
	border-color: rgba(100, 100, 100, 0) !important;
	-webkit-box-shadow: none !important;
	-moz-box-shadow: none !important;
	box-shadow: none !important;
}

.hover-table {
	font-size: 14px;
	color: #6c757d;
	text-align: start;
}

.hover-table td {
	padding-left: 2% !important;
}

.hover-table:hover {
	background-color: #f6f7fa;
}

.header-table {
	text-align: start;
	font-weight: 500;
	color: #6c757d;
	font-size: 15px;
}

.header-table th {
	padding-left: 2% !important;
}

.table-cm {
