.btns-info {
    background-color: #ffffff;
    margin: 20px 25px;
    border-radius: 9px;
    box-shadow: 0px 1px 46px 5px rgb(0 0 0 / 8%);
    padding: 30px;
}
.item-card h3, .item-card p{
    text-decoration: none;
}
.btns-info:hover{
    cursor: pointer;
}

.btns-info h3 {
    font-weight: 800;
    /* font-size: 14px; */
    width: 100%;
    font-size: 20px;
    text-align: left;
}

.btns-info p {
    /* font-size: 14px; */
    width: 100%;
    font-size: 14px;
    text-align: left;
}
.active-btn{
    background-color: #30384e;
    box-shadow: 0px 20px 50px 0px rgb(0 0 0 / 15%);
    transition: 120ms;
    color: white;
}

.btns-info:hover {
    background-color: #30384e;
    text-decoration: none;
    box-shadow: 0px 20px 50px 0px rgb(0 0 0 / 15%);
    transition: 120ms;
    color: white;
}

.icon-service {
    height: 100%;
    width: 65%;
}

@media screen and (max-width:990px) {
    .btns-info h3 {
        font-size: 1.1rem!important;
    }
    .btns-info p {
        font-size: .8rem!important;
    }
    .icon-service {
        width: 3.5rem!important;
    }
}
@media screen and (max-width:760px) {
    .btns-info h3 {
        text-align: center;
    }
}
@media screen and (max-width:540px) {
    .btns-info p {
        text-align: center;
    }
}