
.form-adendar{
    box-shadow: 0px 20px 50px 0px rgb(0 0 0 / 15%);
    padding: 50px 25px;
    margin: 14px 5px;
    background-color: white;
    border-radius: 10px;
}
.container-rowF input, .container-rowF textarea{
    border-color: #212529;
}
.btn-line-send{
    padding: 7px 42px;
    border-radius: 5px;
    font-weight: 500;
    text-align: center;
    border: .9px solid #30384e;
    background: transparent;
    color: #30384e;
    width: 100%;
}
.error{
    color: red;
    font-weight: bold;
}
.error::after{
    content: '*';
}


.btn-line-send:hover{
    color: #ffffff;
    border: .9px solid #212529;
    background-color: #30384e;
    font-weight: 600;
}