.container-carrouselTesti {
    /* margin-top: 2rem; */
    width: 100%;
    height: 30vh!important;
    position: relative;
    /* overflow: hidden; */
}

.container-carrouselTesti .text-testimonio {
    margin: 0 auto;
    font-size: .9rem;
    background-color: #00335e60!important;
    color: #fff;
    padding: 1.5rem 1rem;
    border-radius: 10px;
    text-align: center;
    width: 85%;
}

.container-carrouselTesti .info-cliente {
    width: 82%;
    margin: 1rem auto;
    padding-left: .5rem;
    color: #fff;
    /* padding: 1rem .8rem; */
    /* border-radius: 10px; */
    display: flex;
    align-items: center;
}

.container-carrouselTesti .info-cliente .imgTestimonio1,.container-carrouselTesti .info-cliente .imgTestimonio2,.container-carrouselTesti .info-cliente .imgTestimonio3, .container-carrouselTesti .info-cliente .imgTestimonio4 {
    width: 80px;
    height: 70px;
    background-color: #ffffff00!important;
    margin-right: 2rem;
    border-radius: 100%;
}
.container-carrouselTesti .info-cliente .imgTestimonio1 {
    background-image: url("../../img/user_2.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

}
.container-carrouselTesti .info-cliente .imgTestimonio2{
    background-image: url("../../img/user_3.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

}
.container-carrouselTesti .info-cliente .imgTestimonio3 {
    background-image: url("../../img/user_1.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

}
.container-carrouselTesti .info-cliente .imgTestimonio4 {
    background-image: url("../../img/user_4.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

}
