.container-button {
    width: 100%!important;
    background-color: #ffffff;
    box-shadow: 10px 5px 29px 6px rgb(0 0 0 / 10%);
}

.container-button a {
    text-decoration: none;
    font-weight: 500;
    color: rgb(46, 46, 46);
}

.container-button .btnaction {
    font-weight: 500;
    color: #ffffff;
    margin-inline: 5px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.container-carrouselTesti .info-cliente .nombre-cliente {
    text-align: start;
    width: 75%;
    border-radius: 100%;
}

.container-carrouselTesti .info-cliente .nombre-cliente p {
    margin: 0 0 .1rem 0;
}

.container-carrouselTesti .info-cliente .nombre-cliente p:first-of-type {
    font-weight: 700;
    font-size: 1.5rem;
    color: #fff;
}

.container-carrouselTesti .info-cliente .nombre-cliente p {
    font-weight: 500;
    color: #ebbb1f;
    font-size: 1.1rem;
}

.home_banner {
    /* background-image: url('../../img/header_banner.jpg'); */
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: -100px;
    height: 500px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}

.home_banner h1 {
    font-size: 4rem;
    text-shadow: 5px 6px black;
}

.home_subBanner {
    color: white;
    background-color: black;
    display: flex;
    justify-content: center;
    padding: 10px;
}

.home_content_title {
    display: flex;
    justify-content: center;
}

.home_content_title h2 {
    font-size: 2rem;
}

.home_content_clisters {
    display: flex;
    flex-wrap: wrap;
}

.home_content_clisters div {
    display: flex;
    flex-grow: 1;
    width: 31%;
    height: 250px;
    justify-content: center;
    align-items: center;
    background-color: tomato;
    margin: 1%;
}

.home_content {
    padding: 0 10%;
    margin-top: 2rem;
}

.home_content_clisters div a {
    color: white;
    text-decoration: none;
}

.float-btns {
    background-color: #ffffff;
    box-shadow: 10px 5px 29px 6px rgb(0 0 0 / 10%);
    right: 0;
    /* top: 92%; */
    z-index: 2000;
    width: 100%;
    padding: .8rem;
    display: flex;
    justify-content: flex-end;
    /* height: 4rem;
    /* height: 2rem; */
}

.float-btns div {
    width: 30%;
}

.float-btns div a {
    text-decoration: none;
    color: #254e70;
    font-weight: 600;
}

.btn_buscarGuia {
    border: .12rem solid #254e70;
    /* color: #254e70; */
    color: white;
    padding: 4px 0;
    /* margin-right: 5px; */
    border-radius: 6px;
    /* font-weight: 400; */
    font-weight: 500;
    background-color: #254e70;
    width: 100%;
}

.float-btns a:hover {
    /* color: white; */
    /* background-color: #254e70; */
    transition: 150ms;
    /* font-weight: 500; */
    width: 14%!important;
    /* background-color: #2a5979; */
}

.btn_buscarOficinas {
    border: .12rem solid #254e70;
    color: #254e70;
    width: 12%;
    padding: 4px 0;
    border-radius: 6px;
    /* font-weight: 400; */
    background-color: #ffcd00;
    font-weight: 500;
}

.btn_buscarOficinas:hover {
    /* color: #254e70;
    background-color: #ffcd00; */
    transition: 150ms;
    /* font-weight: 500; */
    width: 14%!important;
    opacity: .95;
    background-color: #ffc400;
}

