    font-size: 2.5rem;
    letter-spacing: -0.3px;
    font-weight: 900;
    color: white;
}

.input-text {
    width: 100% !important;
    padding: 10px 40px !important;
    margin-bottom: 30px!important;
    border-width: 1px !important;
    border-style: solid !important;
    background-color: rgba(255, 255, 255, 0.863) !important;
}

.icon-input {
    position: absolute;
    margin-left: 9px;
    margin-top: 5px;
    z-index: 2;
    display: block;
    width: 1.3rem;
    height: 2.375rem;
    line-height: 2.375rem;
    text-align: center;
    pointer-events: none;
    color: #fdc302;
}

.agendar-cita {
    padding: 50px 0rem;
}

.container-testimonios .text-area {
    /* width: 100%; */
    width: 100%;
    margin-bottom: 1.8rem;
    border-radius: 5px;
    border-color: #a3a3a3;
    padding: .5rem;
    border-width: 1px;
    background-color: rgba(255, 255, 255, 0.863);
}

.form-contact .btn-enviar:hover {
    background-color: #ebbb1f;
}

/* CONTACT */

.pad-left-2 {
    padding-left: 2rem!important;
}

.pad-left-08 {
    padding-left: .8rem!important;
