/*  botones  */

.btn-solid-prymary {
    color: white;
    background-color: #39d18a;
    font-size: 14px;
    font-weight: 500;
    border: 1px solid #16d47b!important;
    border-radius: 4px;
    padding: 2px 0;
}

.btn-solid-prymary:hover {
    color: rgb(255, 255, 255);
    font-weight: 500;
    border: none;
    border: 1px solid #39d18a !important;
    background-color: #32a770;
    transition: 300ms;
    -webkit-box-shadow: 0 2px 6px 0 #48494b28 !important;
    box-shadow: 0 2px 6px 0 #48494b28 !important;
}

