.cotizar_div {
    width: 100%;
    /* height: 70vh; */
    background-color: #f4f5f7;
    padding-top: 2rem;
    padding-bottom: 4rem;
}
#sub-title{
    font-weight: 400;
    letter-spacing: 10px;
    font-size: 1.6rem;
    color: #FCD200;
    font-family:  Sans-serif;
}

.display-none{
    display: none;
}

.container-herramienta{
    padding: 2em 0px 7em 0px;
}
.container-Agendar{
    box-shadow: 0px 5px 50px 2px rgb(0 0 0 / 15%);
    border-radius: 10px;
    background-color: #fff;
    width: 100%;
    height: 100%;
    text-align: start;
    padding: 2rem !important;
    margin: 1.5rem;
}
.container-rowF{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    /* background-color: #30384e; */
}
.container-rowF div{
    width: 49%;
    display: flex;
    /* align-items: center; */

}
.container-rowF .icon{
    position: absolute;
    margin-top: 25px;
    margin-left: 10px;
}
.container-rowF input{
    width: 100%;
    margin-top: 0.8rem;
    margin-bottom: 1rem;
    border-radius: 5px;
    border-color: #a3a3a3;
    padding: .5rem 0 .5rem 2.2rem;
    border-width: 1px;
}
.container-rowF textarea{
    width: 100%;
    margin-bottom: 1.8rem;
    border-radius: 5px;
    border-color: #a3a3a3;
    padding: .5rem;
    border-width: 1px;
}
.btn-enviar{
    background-color: #ffcd00;
    padding: .5rem 0;
    width: 22%;
    text-align: center;
    border-radius: 10px;
    font-weight: 600;
    color: rgb(13, 18, 85);
    border: 1px solid rgb(13, 18, 85);
}
.btn-enviar:hover{
    cursor: pointer;
    background-color: #f5cf35;
}
.btn-enviar p{
    margin: 0;
}

.container-Agendar h5{
    color: #6b6b6b;
    font-weight: 400;
}
.container-Agendar h4{
    color: #030849;
    font-weight: 800;
    font-size: 1.9rem;
}
.contenedor {
    width: 100%;
    display: flex;
    justify-content: center;
}

.flex-center {
    display: flex;
    justify-content: center;
    width: 80%;
    margin: 0 auto;
}

.content-section-btns {
    background-color: #30384e;
    height: 55px;
    margin-top: -4.5%;
}

.row .nav__link {
    align-items: center;
    text-decoration: none;
    padding-left: 0px;
    padding-right: 0px;
    margin-left: 0px;
    margin-right: 0px;
    color: white;
}


.row .nav__link:hover {
    color: #30384e;
    text-decoration: none;
    background-color: #f6cb0b;
    box-shadow: 0px 20px 50px 0px rgb(0 0 0 / 10%);
    font-weight: 700;
    transition: 200ms;
}
.link-covertura{
    text-decoration: none;
    color: #30384e;
}
.link-covertura:hover{
    text-decoration: none;
    color: #ffffff;
}
@media screen and (max-width:1024px) {
    .container-rowF .icon{
        margin-top: 2.5%;
    }
}
@media screen and (max-width:900px) {
    .container-rowF .icon{
        margin-top: 2.7%;
    }
}
@media screen and (max-width:990px) {
    .flex-center {
        width: 90%;
    }
    .display-none{
        display: flex;
    }
    .bts-text{
        display: none;
    }
}

@media screen and (max-width:800px) {
    .flex-center {
        width: 100%!important;
    }
       
}
@media screen and (max-width:780px) {
    .container-rowF .icon{
        margin-top: 25px;
        margin-left: 10px;
    }
       
}
@media screen and (max-width:640px) {
    .container-rowF div{
        flex-direction: column!important;
        width: 100%;
    }
    .container-rowF .icon{
        margin-top: 25px;
        margin-left: 10px;
    }
    .btn-enviar{
        width: 35%!important;
    }
       
}
@media screen and (max-width:550px) {
    .cotizar_div{
        padding-bottom: 6rem!important;
    } 
    .display-none{
        display: flex;
        flex-wrap: wrap;
    }
    .bts-text{
        display: none;
    }
}
@media screen and (max-width:450px) {
    .flex-center {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    

    .flex-center .col-5 {
        flex-direction: row!important;
        flex-wrap: wrap;
        align-items: center!important;
        width: 100%!important;
    }
    .form-container{
        width: 100%;
    }
    .container-calculator{
        width: 90%!important;
    }
    .container-rowF .icon{
        margin-top: 5.5%!important;
        margin-left: 1.4%;
    }
    .container-Agendar{
        width: 90%!important;
    }
       
}
@media screen and (max-width:450px) {
    .container-rowF .icon{
        margin-top: 6.3%!important;
        margin-left: 1.8%;
    }  
}
@media screen and (max-width:360px) {
    .container-rowF .icon{
        margin-top: 7%!important;
        margin-left: 2%;
    }  
}
