

.scroll-section-side-bar{
	overflow-y: auto;
	height: 100vh;
}
.scroll-section-side-bar::-webkit-scrollbar {
	width: 7px;
	           /* width of the entire scrollbar */
  }
  
  .scroll-section-side-bar::-webkit-scrollbar-track {
	background: rgba(255, 255, 255, 0);        /* color of the tracking area */
  }
  
  
  .scroll-section-side-bar::-webkit-scrollbar-thumb {
    background-color: rgba(128, 128, 128, 0.404);    /* color of the scroll thumb */
    border-radius: 20px;       /* roundness of the scroll thumb */
    /* border: 3px solid orange;  creates padding around scroll thumb */

  }

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  padding: 30px 0 0;
  box-shadow: inset -1px 0 0 rgb(0 0 0 / 10%);
  /* overflow-y: scroll; */
  /* margin-bottom: 30px; */
}
.into-items-container, .sidebar {
  /* background-color: #ffffff !important; */
  background-color: #e7e8eb !important;
}

.title-Text{
  font-weight: 600;
  font-size: 15px;
}
.sidebar>div {
  padding-bottom: 20%;
}


.sidebar .nav-link .feather {
  margin-right: 8px;
  color: #686868;
}

.sidebar .nav-item a {
  font-size: .92rem;
}

.sidebar .nav-link {
  font-weight: 600;
  color: #616161;
}

.sidebar .container-nenu {
  padding-top: 20%;
}

.sidebar .title-slide {
  font-weight: 600;
  font-size: 19px;
  text-decoration: underline;
}

.sidebar .into-nav {
  padding-left: 15px;
}

.sidebar .into-items {
  align-items: flex-start;
}

.sidebar .btn-action {
  border: 0px solid rgba(204, 204, 204, 0)!important;
  text-decoration: none;
}

.sidebar .ref-link {
  text-decoration: none;
}

.sidebar .material-icons {
  font-size: 21px;
  color: #585858;
  padding-right: 10px;
  position: relative;
  top: 4px;
}


.text-btn {
  font-size: .89rem;
  color: #555555;
  /* padding-left: 2.2rem; */
}

.text-btn:hover {
  color: #202020;
  transition: 300ms;
}

 .selected {
  color: #202020 !important;
  transition: 300ms;
}

.nav .nav-item .nav-link:hover .material-icons {
  color: #2e2e2e !important;
  transition: 300ms;
}
.text-btn-selected{
  font-size: .89rem;
  color: #2e2e2e;
  font-weight: 600;

  
}
.nav .nav-item .nav-link:hover {
  color: #2e2e2e !important;
  transition: 300ms;
  font-weight: 800;
}

.into-items-container a {
  text-decoration: none;
}

.list-items {
  padding-left: 2.2rem;
}

.text-btn-cerrar {
  color: #474747;
}

.rol-menu {
  font-size: 1.3em!important;
  color: #3b3b3b;
  text-decoration: none;
  /* padding-left: 15px; */
  /* padding-bottom: 5px; */
  font-weight: 800;
  text-decoration: none !important;
}

.text-version{
  color: #161616;
}
.img-slider{
  margin-bottom: 3rem;
}

.separator-rol{
  color: black;
}